import Vue from 'vue'
import VueRouter from 'vue-router'


Vue.use(VueRouter)

  const routes = [
  {
    path: '/',
    name: 'Home',
    component: () => import('../components/Home.vue')
  },
   {
    path: '/chisiamo',
    name: 'Chisiamo',
    component: () => import('../components/Chisiamo.vue')
   },
    {
    path: '/servizi',
    name: 'Servizi',
    component: () => import('../components/Servizi.vue')
    },
    {
      path: '/aree',
      name: 'Aree',
      component: () => import('../components/Aree.vue')
    },
    {
      path: '/eventi',
      name: 'Eventi',
      component: () => import('../components/Eventi.vue')
    },
    {
      path: '/contatto',
      name: 'Contatto',
      component: () => import('../components/Contatto.vue')
    },
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
